.dark-mode {
  background-color: darken($grisOscuro, 10);

  p {
    color: $blanco;
  }
  a,
  h1,
  h2,
  h3,
  h4,
  h5 {
    color: $gris;
  }

  .anuncio,
  .resumen-proyecto {
    background-color: $grisOscuro;
    border: 1px solid $grisOscuro;

    .precio {
      color: $verde;
    }
  }

  .texto-nosotros blockquote {
    color: $blanco;
  }
  
  .formulario {
    label,
    legend,
    p {
      color: $gris;
    }

    input:not([type="submit"]),
    textarea,
    select {
      background-color: $grisOscuro;
      border: 1px solid $grisOscuro;
      color: $blanco;
    }
  }
  p,
  th,
  thead,
  tr,
  td {
    color: $blanco;
  }
  table.propiedades {
    tbody {
        td {
            color: $blanco;
            border-bottom: solid 2px $blanco;
            border-left: solid 8px #1a1a1a;
        }
    }
  }
}

