html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 6.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-bottom: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 7px;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-bottom: 2.5rem;
  display: block;
  border: none;
  border-radius: 7px;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-bottom: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 7px;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-bottom: 2.5rem;
  display: block;
  border: none;
  border-radius: 7px;
  width: 100%;
  margin-bottom: 2rem;
}
.boton-rojo-block:hover {
  background-color: #a40606;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-bottom: 2.5rem;
  display: block;
  border: none;
  border-radius: 7px;
}
.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a,
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4,
.dark-mode h5 {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .resumen-proyecto {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-proyecto .precio {
  color: #71B100;
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}
.dark-mode p,
.dark-mode th,
.dark-mode thead,
.dark-mode tr,
.dark-mode td {
  color: #FFFFFF;
}
.dark-mode table.propiedades tbody td {
  color: #FFFFFF;
  border-bottom: solid 2px #FFFFFF;
  border-left: solid 8px #1a1a1a;
}

/** UI **/
.no-webp.inicio {
  background-image: url(../img/gobierno.jpg);
}

.webp .inicio {
  background-image: url(../img/gobierno.webp);
  box-shadow: 10px 10px 30px 15px #000000;
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 900;
  font-size: 4.5rem;
  line-height: 2;
  max-width: 60rem;
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.5);
  -webkit-text-stroke: 2px black;
}

/* Nueva clase personalizada */
.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .contenedor-footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyrigth {
  color: #FFFFFF;
  text-align: center;
}

.bd-placeholder-img {
  font-size: 1.125rem;
  text-anchor: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

@media (min-width: 768px) {
  .bd-placeholder-img-lg {
    font-size: 3.5rem;
  }
}
.b-example-divider {
  width: 100%;
  height: 3rem;
  background-color: rgba(0, 0, 0, 0.1);
  border: solid rgba(0, 0, 0, 0.15);
  border-width: 1px 0;
  box-shadow: inset 0 0.5em 1.5em rgba(0, 0, 0, 0.1), inset 0 0.125em 0.5em rgba(0, 0, 0, 0.15);
}

.b-example-vr {
  flex-shrink: 0;
  width: 1.5rem;
  height: 100vh;
}

.bi {
  vertical-align: -0.125em;
  fill: currentColor;
}

.nav-scroller {
  position: relative;
  z-index: 2;
  height: 2.75rem;
  overflow-y: hidden;
}

.nav-scroller .nav {
  display: flex;
  flex-wrap: nowrap;
  padding-bottom: 1rem;
  margin-top: -6px;
  overflow-x: auto;
  text-align: center;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}

.btn-bd-primary {
  --bd-violet-bg: #712cf9;
  --bd-violet-rgb: 112.520718, 44.062154, 249.437846;
  --bs-btn-font-weight: 600;
  --bs-btn-color: var(--bs-white);
  --bs-btn-bg: var(--bd-violet-bg);
  --bs-btn-border-color: var(--bd-violet-bg);
  --bs-btn-hover-color: var(--bs-white);
  --bs-btn-hover-bg: #6528e0;
  --bs-btn-hover-border-color: #6528e0;
  --bs-btn-focus-shadow-rgb: var(--bd-violet-rgb);
  --bs-btn-active-color: var(--bs-btn-hover-color);
  --bs-btn-active-bg: #5a23c8;
  --bs-btn-active-border-color: #5a23c8;
}

.bd-mode-toggle {
  z-index: 1500;
}

.bd-mode-toggle .dropdown-menu .active .bi {
  display: block !important;
}

.footerd {
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
.footerd a {
  margin-right: 5rem;
}
.footerd a:hover {
  color: #71B100;
  font-size: 2rem;
  transition: ease 0.2s all;
}

.nav-link {
  color: #000;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  margin-right: 9rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.5rem;
  display: block;
  text-align: center;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5); /* Sombreado al texto */
  margin-right: 0rem;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 8rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .navegacion a {
    margin-right: 3rem;
  }
}
.navegacion a:hover {
  color: #71B100;
  font-size: 2rem;
  transition: ease 0.2s all;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
  width: auto;
  margin: 0 auto;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: minmax(0, auto) minmax(0, auto) minmax(0, auto);
    grid-template-rows: minmax(0, auto);
    gap: 1rem;
  }
  .anuncio {
    margin: 0;
    display: flex;
    flex-direction: column;
  }
  .anuncio img {
    height: 200px;
    overflow: hidden;
  }
  .contenido-anuncio {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  .card-description {
    flex-grow: 1;
  }
}
.contenedor-anuncios {
  width: 90%;
  max-width: 1200px;
  margin: 20px auto;
}

.anuncio img {
  border-radius: 8px 8px 0 0;
}

.anuncio {
  background-color: #eeeeee;
  border: 1px solid #fbfbfb;
  margin-bottom: 2rem;
  border-radius: 8px;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3,
.anuncio p {
  margin: 0;
  word-wrap: break-word;
}
.anuncio h3 {
  font-weight: 900;
  margin-bottom: 3rem;
  font-size: 2.3rem;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}

.progress {
  --bs-progress-height: 2rem;
  margin-bottom: 1rem;
}

.fecha {
  color: #e1e1e1;
  background-color: #77dd77;
  font-size: 3rem;
  font-weight: 900;
  -webkit-text-stroke: 2px #000;
  text-align: center;
}

.titulo {
  font-weight: 900;
  color: #fb8500;
}

.abajo {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url(../img/banner1.2.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/banner1.2.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 10px 10px 30px 15px #000000;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #E08709;
  text-shadow: 4px 4px 6px rgba(0, 0, 0, 0.5);
}
.imagen-contacto p {
  font-size: 2.8rem;
  color: #E08709;
  text-shadow: 4px 4px 6px rgba(0, 0, 0, 0.5);
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-proposito {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-proposito {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-proposito:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.texto-entrada span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(/build/img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

textarea {
  height: 20rem;
}

.forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.forma-contacto input[type=radio] {
  margin: 0;
  width: auto;
}

.s {
  margin-top: 2rem;
  margin-right: auto;
  padding: 0;
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.user-img {
  width: 50px;
  border-radius: 100%;
  border: 1px solid #eee;
}

.sidebar {
  position: absolute;
  top: 8rem;
  left: 0;
  height: 100vh;
  width: 80px;
  background-color: rgba(var(--bs-dark-rgb));
  padding: 0.4rem 0.8rem;
  transition: all 0.5s ease;
}

.sidebar.active ~ .main-content {
  left: 280px;
  width: calc(100% - 250px);
}

.sidebar.active {
  width: 270px;
}

.sidebar #btn {
  position: absolute;
  color: #fff;
  top: 1rem;
  left: 50%;
  font-size: 1.2rem;
  line-height: 50px;
  transform: translate(-50%);
  cursor: pointer;
}

.sidebar.active #btn {
  left: 90%;
}

.sidebar .top .logo {
  color: #fff;
  display: flex;
  height: 50px;
  width: 100%;
  align-items: center;
  pointer-events: none;
  opacity: 0;
}

.sidebar.active .top .logo {
  opacity: 1;
}

.top .logo i {
  font-size: 2rem;
  margin-right: 5px;
}

.user {
  display: flex;
  align-items: center;
  margin: 1rem 0;
}

.user p {
  color: #fff;
  opacity: 1;
  margin-left: 2rem;
  margin-top: 0;
  margin-bottom: 0;
}

.bold {
  font-weight: 800;
}

.sidebar p {
  opacity: 0;
}

.sidebar.active p {
  opacity: 1;
}

.sidebar ul li {
  position: relative;
  list-style-type: none;
  height: 50px;
  width: 90%;
  margin: 8rem auto;
  line-height: 50px;
}

.sidebar ul li a {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  border-radius: 0.8rem;
  white-space: nowrap;
}

.sidebar ul li a:hover {
  background-color: #fff;
  color: #0d1218;
}

.sidebar ul li a i {
  min-width: 50px;
  height: 50px;
  border-radius: 12px;
  line-height: 50px;
}

.sidebar .nav-item {
  opacity: 0;
}

.sidebar.active .nav-item {
  opacity: 1;
}

.sidebar ul li .tooltipp {
  position: absolute;
  left: 125px;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0.5rem 0.8rem rgba(0, 0, 0, 0.2);
  border-radius: 0.6rem;
  padding: 0.4rem 1.2rem;
  line-height: 1.8rem;
  z-index: 20;
  opacity: 0;
}

.sidebar ul li:hover .tooltipp {
  opacity: 1;
}

.sidebar.active ul li .tooltipp {
  display: none;
}

.main-content {
  position: relative;
  background-color: #eee;
  min-height: 100vh;
  top: 0;
  left: 80px;
  transition: all 0.5s ease;
  width: calc(100% - 80px);
  padding: 1rem;
  background-image: url(../img/gobierno.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

/* Media Queries */
@media (max-width: 768px) {
  .sidebar {
    width: 60px;
  }
  .sidebar.active {
    width: 200px;
  }
  .sidebar.active ~ .main-content {
    left: 200px;
    width: calc(100% - 200px);
  }
  .main-content {
    left: 60px;
    width: calc(100% - 60px);
  }
  .sidebar #btn {
    left: 50%;
    font-size: 1rem;
  }
  .sidebar.active #btn {
    left: 80%;
  }
  .sidebar ul li {
    margin: 4rem auto;
  }
  .user p {
    margin-left: 1rem;
  }
  .sidebar ul li a {
    white-space: normal; /* Permitir que el texto se divida en varias líneas si es necesario */
    line-height: initial;
  }
}
@media (max-width: 480px) {
  .sidebar {
    width: 50px;
  }
  .sidebar.active {
    width: 150px;
  }
  .sidebar.active ~ .main-content {
    left: 150px;
    width: calc(100% - 150px);
  }
  .main-content {
    left: 50px;
    width: calc(100% - 50px);
  }
  .sidebar #btn {
    font-size: 0.8rem;
  }
  .sidebar ul li {
    margin: 2rem auto;
  }
  .sidebar ul li a {
    font-size: 0.8rem;
  }
}
/**Paginas Internas**/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
  border-left: 1px solid #000000;
  text-align: center;
}
table.propiedades thead th:first-child {
  border-left: unset;
}
table.propiedades tbody td {
  text-align: center;
  border-bottom: solid 2px #000000;
  border-left: solid 8px #FFFFFF;
  height: 18rem;
  font-size: 1.8rem;
}
table.propiedades tbody td:first-child {
  font-weight: 700;
  color: #71B100;
  border-bottom: solid 2px #71B100;
}
table.propiedades tbody td:nth-child(2) {
  text-transform: uppercase;
}
table.propiedades tbody td:last-child {
  padding: 1rem 0;
}
table.propiedades tbody td:last-child a {
  margin: 0;
  width: 100%;
}
table.propiedades tbody td:last-child a:first-child {
  margin-bottom: 1rem;
}
table.propiedades .imagen-tabla {
  width: 20rem;
  margin: 0 auto;
}

.imagen-small {
  max-width: 20rem;
}