table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;

  thead {
    background-color: $verde;

    th {
      color: $blanco;
      padding: 2rem;
      border-left: 1px solid $negro;
      text-align: center;
    }

    th:first-child {
      border-left: unset;
    }
  }

  tbody {
    td {
      text-align: center;
      border-bottom: solid 2px $negro;
      border-left: solid 8px $blanco;
      height: 18rem;
      font-size: 1.8rem;
    }

    td:first-child {
      font-weight: $bold;
      color: $verde;
      border-bottom: solid 2px $verde;
    }

    td:nth-child(2) {
      text-transform: uppercase;
    }

    td:last-child {
      padding: 1rem 0;

      a {
        margin: 0;
        width: 100%;
      }
      a:first-child {
        margin-bottom: 1rem;
      }
    }
  }

  .imagen-tabla {
    width: 20rem;
    margin: 0 auto;
  }
}
.imagen-small {
  max-width: 20rem;
  
}
